import React from 'react';
import { GridHorizontalDivider } from 'Components/UI';
import { SearchUserGroup } from 'Components/UI/SearchBar/SearchUserGroup';
import { Button, Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimesCircle } from '@fortawesome/free-solid-svg-icons';
import { getUsersToAddToGroup } from 'Pages/API/Calls/User';

interface IState {
  selectedItems: any;
  comboArray: any[];
}

interface IProps {
  onSave: (items) => void;
  onClose: () => void;
}

const useStyles = makeStyles(() => ({
  selectedItems: {
    borderRadius: 20,
    fontSize: 13,
    backgroundColor: '#d1d4db',
    padding: '5px 10px'
  },
  xIcon: {
    fontSize: 16
  }
}));
console.log(useStyles);

export class AddUserGroupBody extends React.Component<IProps, IState> {

  constructor(props) {
    super(props);

    this.state = {
      selectedItems: [],
      comboArray: [],
    };
  }

  componentDidMount() {
    getUsersToAddToGroup().then(res => {
      let data = {
        body: res
      }
      let newChoices: any = [];
      console.log(data)
      data.body.map(c => {
        let beauty = {
          _id: c['_id'],
          name: c['first_name'] + " " + c['middle_name'] + " " + c['last_name'],
          full_name: c['full_name'],
          last_name: c['last_name'],
          middle_name: c['middle_name'],
          first_name: c['first_name'],
          job_title: c['job_title'],
          username: c['username'],
          department: c['department'],
          effectivity_date: c['effectivity_date'],
          email: c['email'],
          employee_id: c['employee_id'],
          is_active: c['is_active'],
          location: c['location'],
          main_module: c['main_module'],
          main_role: c['main_role'],
          pmaker_uid: c['pmaker_uid'],
          profile_pic: c['profile_pic'],
          roles: c['roles'],
          status: c['status']
        }
        newChoices.push(beauty)
      })
      console.log(newChoices)
      this.setState({
        comboArray: newChoices
      })
      console.log(this.state.comboArray)

    }).catch(err => {
      console.log(err);
    })
  }

  handleCloseSideModalBlock = () => {
    this.props.onClose();
  };

  handleOnSearch = (list) => {
    console.log(list)
    if (!list || typeof list !== 'object' || (list && !list.name) || (list && !list.username)) return;
    let arr = this.state.selectedItems;
    arr.push(list);
    this.setState({
      selectedItems: arr
    })
    console.log(arr);
  }

  handleRemoveItem = (index) => {
    let arr = this.state.selectedItems;
    arr.splice(index, 1);
    this.setState({
      selectedItems: arr
    })
  }

  handleOnSaveItems = () => {
    this.props.onSave(this.state.selectedItems);
    this.setState({
      selectedItems: []
    })
  }


  render(): JSX.Element {
    const classes = {
      buttonsContainer: {
        padding: '1em',
      },
      selectedContainer: {
        padding: '3em 0 2em'
      },
      selectedItems: {
        borderRadius: 20,
        fontSize: 13,
        backgroundColor: '#d1d4db',
        padding: '5px 10px',
        margin: '5px',
        display: 'inline-block',
      },
      selectedText: {
        paddingRight: '5px'
      },
      saveButton: {
        width: '135px',
        fontSize: '14px',
        color: '#fff',
      },
      cancelButton: {
        width: '135px',
        fontSize: '14px',
      }
    };

    return (
      <div style={{ minHeight: 400, minWidth: 400 }}>
        <SearchUserGroup
          searchLabel={'Search user group or account'}
          onSearch={this.handleOnSearch}
          comboBoxMode={true}
          comboListsArray={this.state.comboArray}
        />
        <div style={classes.selectedContainer}>
          {this.state.selectedItems.length > 0 ?
            <div>
              <label>Selected Users</label>
            </div>
            : null}
          {this.state.selectedItems.map((item, index) =>
            <div style={classes.selectedItems}>
              <span style={classes.selectedText}>{item.name}</span>
              <span onClick={() => this.handleRemoveItem(index)}>
                <FontAwesomeIcon icon={faTimesCircle} />
              </span>
            </div>
          )
          }
        </div>
        <GridHorizontalDivider height="150px" />
        <Grid container spacing={2} alignItems="flex-start" justify="center">
          <Grid item xs={4}>
            <Button style={classes.cancelButton} variant="outlined" color="secondary" onClick={() => this.handleCloseSideModalBlock()}>Cancel</Button>
          </Grid>
          <Grid item xs={4}>
            <Button disabled={this.state.selectedItems.length < 1 ? true : false}
              style={classes.saveButton}
              variant="contained"
              color="primary"
              onClick={() => this.handleOnSaveItems()}>
              Add
                        </Button>
          </Grid>
        </Grid>
      </div>
    );
  }
}
