import * as React from 'react';
import { Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
    faSearch,
} from '@fortawesome/free-solid-svg-icons';

export interface ISearchUserGroupProps {
    placeholder?: string;
    searchLabel?: string;
    onSearch: (event) => void;
    comboBoxMode?: boolean;
    comboListsArray?: any;
    onChange?: (value) => void;
}

const useStyles = makeStyles(theme => ({
    gridContainerNoPadding: {
        padding: theme.spacing(1),
    },
    searchIcon: {
        position: "absolute",
        left: "15px",
        top: "50%",
        transform: "translateY(-50%)",
        fontSize: "20px",
        color: "#b0b0b0"
    },
    searchContainer: {
        borderRadius: "3px",
        position: "relative",
    },
    searchBox: {
        display: "block",
        padding: "12px 12px 12px 40px",
        borderRadius: "3px",
        fontSize: "14px",
        width: "100%",
        border: '1px solid #c4c4c4',
        outline: 'none',
        overflow: "hidden",
        '&:hover': {
            border: "1px solid rgb(39, 46, 76)",
        },
        '&:focus': {
            outline: "2px solid #3AB77D"
        }
    },
    searchLabel: {
        fontSize: 14,
        display: 'block',
        padding: '5px 0',
    },
    comboBoxContainer: {
        position: 'absolute',
        top: '100%',
        left: '0%',
        width: '100%',
        backgroundColor: '#fff',
        borderRadius: '2px',
        border: '1px solid #c4c4c4',
        padding: '10px',
        maxHeight: 140,
        overflowY: 'auto'
    },
    list: {
        fontSize: 12,
        padding: '5px',
        '&:hover': {
            backgroundColor: '#dddfe3',
        }
    }
}));


export const SearchUserGroup: React.FC<ISearchUserGroupProps> = (props: ISearchUserGroupProps): JSX.Element => {
    const {
        placeholder,
        searchLabel,
        onSearch,
        comboListsArray,
        comboBoxMode,
        onChange,
    } = props;

    const classes = useStyles();
    const [newComboList, setNewComboList] = React.useState<any>([]);
    const [searchValue, setSearchValue] = React.useState<any>(null)
    const [typingTimeout, setTypingTimeout] = React.useState<any>(0);

    const handleKeyPress = (event) => {
        if (event.target.value.length >= 3 || event.target.value.length === 0) {
            let value = event.target.value;
            onChange && onChange(value)

            if (typingTimeout) {
                clearTimeout(typingTimeout);
            }

            setTypingTimeout(setTimeout(function () {
                if (comboBoxMode) {
                    if (comboListsArray) {
                        searchJSONValue(value);
                        return;
                    } else {
                        return false;
                    }
                } else {
                    return false;
                }

            }, 2000));
        }
    }

    const handleSearch = (event) => {

        let value = event.target.value;
        onChange && onChange(value)

        if (comboBoxMode) {
            if (comboListsArray) {
                searchJSONValue(value);
                return;
            } else {
                return false;
            }
        } else {
            return false;
        }

    }

    const searchJSONValue = (value) => {

        var combo = comboListsArray.filter(list => {
            return checkMatchString(value, list.name)
        })
        setNewComboList(combo)
    }

    const checkMatchString = (value, jsonValue) => {

        if (value !== "") {
            var newRegEx = new RegExp(value.toLowerCase(), "i");
            var match = jsonValue.toLowerCase().match(newRegEx);
            return match ? true : false;
        }

        return false
    }

    const searchCombo = (list) => {
        onSearch(list)
        setSearchValue('');
        setNewComboList([]);
    }

    console.log(searchValue)
    return (
        <>
            <Grid container xs={12} style={{ padding: "10px 0" }}>
                <Grid item xs={12}>
                    {searchLabel ? <label className={classes.searchLabel}>{searchLabel}</label> : null}
                </Grid>
                <Grid item xs={12} className={classes.searchContainer} >
                    <FontAwesomeIcon icon={faSearch} className={classes.searchIcon} />
                    <input
                        type={'search'}
                        placeholder={placeholder}
                        value={searchValue}
                        onChange={(event) => {
                            handleKeyPress(event)
                            setSearchValue(event.target.value)
                        }}
                        onKeyUp={(event) => {
                            let code = event.keyCode || event.which
                            if (code == 13 && searchValue.length >= 3) {
                                handleSearch(event)
                            }
                        }}
                        className={classes.searchBox}
                    />

                    {comboBoxMode && newComboList.length > 0 ?
                        <div className={classes.comboBoxContainer}>
                            <ul style={{ padding: 0, margin: 0, listStyle: 'none' }}>
                                {
                                    newComboList && newComboList.map(list => (
                                        <li className={classes.list} onClick={() => searchCombo(list)}>{list.name}</li>
                                    ))
                                }
                            </ul>
                        </div>
                        : comboBoxMode && newComboList.length === 0 && searchValue !== null && searchValue !== '' ? (
                            <div className={classes.comboBoxContainer}>No Results</div>
                        ) : null
                    }
                </Grid>
            </Grid>
        </>
    )
}