export default function App() {
  const [selectedMenu, setSelectedMenu] = useState('');
  const [userName, setUsername] = useState('');
  const [fullName, setFullName] = useState('');
  const [profilePic, setProfilePic] = useState('');
  // const [isRedirect, setIsRedirect] = 'useState'(false);
  // console.log(selectedMenu , setSelectedMenu);

  // const RenderComponent = (selected: string, Component) => {
  //   return class App extends React.Component {
  //     componentDidMount() {
  //       setSelectedMenu(selected)
  //     }

  //     render() {
  //       return <Component {...this.props} />
  //     }
  //   }
  // }

  React.useEffect(() => {
    Utils.StorageService('user_data', 'username').then(response => {
      setUsername(response.result);
    })
    Utils.StorageService('user_data', 'full_name').then(response => {
      setFullName(response.result);
    })
    Utils.StorageService('user_data', 'profile_pic').then(response => {
      setProfilePic(response.result);
    })

    history.listen((location) => {
      if (location.hash.includes('dashboard')) {
        setSelectedMenu('dashboard');
      }

      if (
        location.hash.includes('users') ||
        location.hash.includes('roles') ||
        location.hash.includes('add-roles') ||
        location.hash.includes('group') ||
        location.hash.includes('adduser')
      ) {
        setSelectedMenu('users')
      }

      if (
        location.hash.includes('lookup')
      ) {
        setSelectedMenu('lookup')
      }


    })

  }, [])

  const onLogout = () => {
    window.location.replace('../index.html#/');
  };

  let routes = [
    {
      path: "/lookup",
      component: LookupPage,
    },
    {
      path: '/',
      component: DashboardPage,
      exact: true,
    },
    {
      path: '/users',
      component: UsersPage,
    },
    {
      path: '/roles/:id',
      component: RolesPage,
    },
    {
      path: '/add-roles',
      component: AddRolesPage,
    },
    {
      path: '/account/:id',
      component: Account,
    },
    {
      path: '/group/:id',
      component: Group,
    },
    {
      path: '/profile',
      component: Profile,
    },
    {
      path: '/adduser',
      component: AddUserPage,
    },
    // ,
    {
      path: '/dashboard',
      component: DashboardPage,
    }
  ];
  const prefix = '/system-admin';
  routes.map(route => (route.path = `${prefix}${route.path}`));

  return (

    <div style={{ backgroundColor: 'white' }}>
      {/* { isRedirect &&
        <Redirect
          to={{
            pathname: '/system-admin/users',
            // state: { from: this.state.redirect_data },
          }}
        />
      } */}
      <ThemeProvider theme={theme}>
        <StylesProvider injectFirst>
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <Helmet titleTemplate="NEO - %s" defaultTitle="NEO">
              <meta name="description" content="NEO application" />
            </Helmet>
            <CssBaseline>
              <Components.UI.PageLayout
                selectedMenu={selectedMenu}
                setSelectedMenu={setSelectedMenu}
                history={history}
                page={'system admin'}
                onLogout={onLogout}
                username={userName}
                fullname={fullName}
                profilePic={profilePic}
              // onAccounts={() => {
              //   console.log('Accounts clicked')
              // }}
              >
                <Switch>
                  {renderRoutes(routes)}
                  <Route path="" component={NotFoundPage} />
                </Switch>
              </Components.UI.PageLayout>
            </CssBaseline>
          </MuiPickersUtilsProvider>
        </StylesProvider>
      </ThemeProvider>
    </div>
  );
}
