import React from 'react'

// Material UI imports
import {
    Grid,
    TextField,
    Typography,
    Button,
    // Link,
    // InputAdornment
} from '@material-ui/core'

// Relative imports
import { ViewGroupStyles } from '../styles'
import {
    columns,
    columnsUser,
    pageSizes,
    COLUMN_EXTENSIONS,
    COLUMN_EXTENSIONS_USERS
} from './ViewGroup.const'
import { TableComponent } from '../Components/TableComponent'
import moment from 'moment'
import { SideModalBlock } from 'Components/UI';
import { AddRoleGroupBody } from './AddRoleGroupBody';
import { AddUserGroupBody } from './AddUserGroupBody';
import { ModalComponent } from 'Components/UI/ModalComponent/ModalComponent';


// Imports for Edit Group
import {
    MuiPickersUtilsProvider,
    KeyboardDatePicker,
} from '@material-ui/pickers';
import MomentUtils from '@date-io/moment';
import { UI } from 'Components';
import { BasicTextField } from 'Components/UI';
// import Modal from '@material-ui/core/Modal';
import { getDepartments } from 'Pages/API/Calls/User';
// import { updateGroup } from 'Pages/API/Calls/Group'
import { makeStyles } from '@material-ui/core/styles'
import ErrorIcon from '@material-ui/icons/Error';
// import CircularProgress from '@material-ui/core/CircularProgress';
 
const useStyles = makeStyles(() => ({
    label: {
        color: '#3C394A',
        fontSize: 12,
        opacity: .6,
        marginBottom: 5
    },
    button: {
		margin: '0 10px',
		minHeight: '42px',
		minWidth: '150px',
	},
    userAlignRight: {
        textAlign: 'right',
    },
    input: {
    },
    value: {
        color: '#3C394A',
        fontSize: 16,
        fontWeight: 800
    },
    linkAction: {
        color: '#3AB77D',
        textDecoration: 'underline',
        fontSize: 12,
        cursor: 'pointer'
    },
    circle: {
        width: 8,
        height: 8,
        background: '#3C394A',
        borderRadius: '100%',
        display: 'inline-block',
        marginRight: 3,
        marginTop: 5
    },
        saveButton: {
        width: '135px',
        fontSize: '14px',
        color: '#fff',
    },
    cancelAdd: {
        width: '135px',
        fontSize: '14px',
        color: '#ffffff',
        marginRight: '20px',
        backgroundColor: '#3C394A',
        '&:hover': {
        backgroundColor: '#302e3c'
        }
    },
    errorLabel: {
        color: '#E53935',
        fontSize: '12px'
    },
    errorIcon: {
        color: '#E53935'
    },
    padlock: {
        color: '#3AB77D',
        fontSize: 18,
        position: 'relative',
        top: -3,
        marginRight: 5
    },
    linkUnlock: {
        marginTop: 10,
        display: 'block'
    }
}));


interface IProps {
    activeMenu: string,
    data: any,
    onSaveRolesToGroup: (items, cb) => void,
    onSaveUserToGroup: (items, cb) => void,
    mode: string, // view and edit modes
    // setGroupData: (value: any) => void,  // setGroupData for edit mode
    setGroupMode: (e) => void,
    dataTmp: any,
    setDataTmp: (value: any) => void,
    onSaveGroupUpdate: (items) => void,
    setIsLoading: any,
    onDeleteRole: (items, cb) => void,
    onDeleteUser: (items, cb) => void,
}

declare global {
    interface ObjectConstructor {
        fromEntries(xs: [string|number|symbol, any][]): object
    }
}

export const ViewGroup: React.FC<IProps> = (props: IProps): JSX.Element => {
    const {
        activeMenu,
        data,
        onSaveRolesToGroup,
        onSaveUserToGroup,
        setGroupMode,
        dataTmp,
        setDataTmp,
        onSaveGroupUpdate,
        // setGroupData
        onDeleteRole,
        onDeleteUser
    } = props
    const classesEdit = useStyles()
    const classes = ViewGroupStyles()
    const [detailsAndRolesShowBlock, setDetailsAndRolesShowBlock] = React.useState('none');
    const [addUserSideBlock, setAddUserSideBlock] = React.useState('none');
    const [confirmationModalProps, setConfirmationModalProps] = React.useState({
        title: '',
        message: '',
        open: false,
        method: () => { }
    });

    React.useEffect(() => {
        setAddUserSideBlock('none')
        setDetailsAndRolesShowBlock('none')
    }, [activeMenu])

    const handleClickAdd = () => {
        if (activeMenu === 'Users') {
            // Show modal of add user to group
            console.log("IN USERS")

            setAddUserSideBlock('block');
            console.log(addUserSideBlock)
        }

        if (activeMenu === 'Details and Roles') {
            // Show modal of add roles to group
            setDetailsAndRolesShowBlock('block')
            console.log("IN DDETAILS AND ROLES")
        }


    }

    const onSaveRoleToGroup = (data) => {
        onSaveRolesToGroup(data, setDetailsAndRolesShowBlock);
        setDetailsAndRolesShowBlock('none')
    }

    /*********************
        Get Departments
    **********************/
    React.useEffect(() => { 

        

        getDepartments().then((res: any) => {
            let choices: any = [];
            if(!res.error) {
                res.forEach(d => {
                    let beauty = {
                        label: d.name,
                        value: d.name
                    }
                    choices.push(beauty)

                })
                setDepartments(choices)
            }
        })
        setSelectedDepartment({
            label: dataTmp.department,
            value: dataTmp.department
        })
        
        if(data.group.hasOwnProperty('effectivity_date') && data.group.effectivity_date !== undefined || null){
            setSelectedDate(dataTmp.effectivity_date)
        }
        if(data.group.hasOwnProperty('expiry_date') &&  data.group.expiry_date !== undefined || null){
            setSelectedDate2(dataTmp.expiry_date)
        } else {
            setSelectedDate2('');
        }
    }, [dataTmp])

    // Hooks for Edit Group
    const [selectedDate, setSelectedDate] = React.useState<any>('');
    const [selectedDate2, setSelectedDate2] = React.useState<any>('')
    const [errorMessages, setErrorMessages] = React.useState<any>({
        description: '',
        department: '',
        effectivity_date: '',
    })
    const [fieldValidation, setFieldValidation] = React.useState<any>({
        description: '',
        department: '',
        effectivity_date: ''
    })
    const [cModalProps, setCModalProps] = React.useState({
        open: false,
        title: '',
        message: '',
        buttonLeft: '',
        methodLeft: () => { },
        buttonRight: '',
        methodRight: () => { }

    })
    const [departments, setDepartments] = React.useState<any>(
        [
            {
                label: ''
            }
        ]
    );
    const [selectedDepartment, setSelectedDepartment] = React.useState<any>({
        
    })
    const [dataChanges, setDataChanges] = React.useState<any>({});
    /********** END **********/

    /***********************
     Handles for Edit Group
    ************************/
    const handleAll = (e, prop) => {
        setDataTmp({
            ...dataTmp,
            [prop]: e.target.value
        })
        setDataChanges({
            ...dataChanges,
            [prop]: e.target.value
        })
    }


    const handleDepartment = (selected: any) => {
        setSelectedDepartment(selected)
        
        if(selected.__isNew__){
            setDataChanges({
                ...dataChanges,
                department: selected.value
            })
            setDataTmp({
                ...dataTmp,
                department: selected.value
            })
        } else {
            setDataChanges({
                ...dataTmp,
                department: selected.value 
            })
            setDataTmp({
                ...dataTmp,
                department: selected.value
            })
        }
    }

    const numberChange = (e) => {
        const re = /^[0-9\b]+$/;
        console.log(`amount limit: `, e.target.value)
        // setAmountLimit(e.target.value)

        if((e.target.value === '') || re.test(e.target.value)){
            setDataTmp({
                ...dataTmp,
                amount_limit: e.target.value
            })
            setDataChanges({
                ...dataChanges,
                amount_limit: e.target.value
            })
        }
    }
    const handleEffectivityDateChange = (e) => {
        setSelectedDate(moment(e).toISOString())
        console.log(e.toISOString())
        setDataTmp({
            ...dataTmp,
            effectivity_date: moment(e).toISOString()
        })
        setDataChanges({
            ...dataChanges,
            effectivity_date: moment(e).toISOString()
        })
    }
    const handleExpireDateChange = (e) => {
        setSelectedDate2(moment(e).toISOString())

        setDataTmp({
            ...dataTmp,
            expiry_date: moment(e).toISOString()
        })
        setDataChanges({
            ...dataChanges,
            expiry_date: moment(e).toISOString()
        })
    }

    const validationAlgorithm = (bodyRequest) => {
        let optionalFields = ['expiry_date', 'amount_limit'];
        let valid: any = [];
        let invalid: any = [];

        let payloadArray = Object.entries(bodyRequest);

        payloadArray.forEach((payloadObject: any) => {
            if(!optionalFields.includes(payloadObject[0])){
                if(payloadObject[1]) {
                    payloadObject[1] = true
                    valid.push(payloadObject)
                } else {
                    payloadObject[1] = false
                    invalid.push(payloadObject)
                }
            }
        })

        let preValid: any = Object.assign(Object.fromEntries(valid), Object.fromEntries(invalid));

        setFieldValidation(preValid);
        console.log(preValid)
        setErrorMessages({
            description: !preValid.description ? 'Invalid Input' : '',
            department: !preValid.department ? 'Invalid Input' : '',
            effectivity_date: !preValid.effectivity_date ? 'Invalid Input' : ''
        })

        for (var f in preValid)
            if(!preValid[f]) return false;
        return true;
    }
    
    /*********************************
        End for Edit Mode Handlers
    **********************************/

    /****************************************
      Successful Update Confirmation Modals
    *****************************************/
    const handleChangeMode = () => {
        console.log('Clicked cancel')
        setCModalProps({
            ...cModalProps,
            open: false
        })
    }
    const handleCancel = () => {
        setGroupMode('view')
        setCModalProps({
            ...cModalProps,
            open: false
        })
        setDataTmp({
            ...dataTmp,
            department: data.group.department,
            description: data.group.description,
            effectivity_date: data.group.effectivity_date,
            expiry_data: data.group.expiry_data,
            amount_limit: data.group.amount_limit
        })
        setFieldValidation({
            description: '',
            department: '',
            effectivity_date: ''
        })
        setDataChanges({})
    }
    const handleSubmit = () => {
        setCModalProps({
            ...cModalProps,
            open: false
        })
        let groupData = {
            ...dataTmp,
            ...dataChanges 
        }

        setGroupMode('view')
        onSaveGroupUpdate(groupData)
        setDataChanges({})

    }

    const handleConfirmation = () => {

        let data = {
            ...dataTmp,
            ...dataChanges
        } 

        if(validationAlgorithm(data)) {
            setCModalProps({
                ...cModalProps,
                open: true,
                title: 'Confirm Changes',
                message: "Are you sure you want to update this group's details with these changes?",
                buttonLeft: 'Cancel',
                methodLeft: handleChangeMode,
                buttonRight: 'Proceed',
                methodRight: statusCheck
            })
        } else {
            setCModalProps({
                ...cModalProps,
                open: true,
                title: 'Confirm Changes',
                message: "Are you sure you want to update this group's details with these changes?",
                buttonLeft: 'Cancel',
                methodLeft: handleChangeMode,
                buttonRight: 'Proceed',
                methodRight: statusCheck
            })
        }
        
    }

    const showDeactivate = () => {
        setCModalProps({
            ...cModalProps,
            open: true,
            title: 'Deativate Group',
            message: 'Would you like to continue deactivating this group?',
            buttonLeft: 'Cancel',
            methodLeft: handleChangeMode,
            buttonRight: 'Deactivate',
            methodRight: handleSubmit
        })
    }

    const showReactivate = () => {
        setCModalProps({
            ...cModalProps,
            open: true,
            title: 'Reactivate Group',
            message: 'Would you like to continue reactivating this group?',
            buttonLeft: 'Cancel',
            methodLeft: handleChangeMode,
            buttonRight: 'Reactivate',
            methodRight: handleSubmit
        })
    }

    const statusCheck = () => {
        if(moment(selectedDate2).isSameOrBefore() && data.group.status.toLowerCase() !== 'deactivated') {
            showDeactivate();
        } else if (
            (moment(selectedDate2).isSameOrAfter() && data.group.status.toLowerCase() !== 'active' || (selectedDate2) === null && data.group.status.toLowerCase() !== 'active')
        ) {
            if(data.group.status.toLowerCase() === 'awaiting activation') {
                handleSubmit()
            } else {
                showReactivate();
            }
        } else {
            handleSubmit()
        }
    }
    /*********************************
        End of Confirmation Modals
    **********************************/
    const handleDelete = (data) => {
        handleCloseConfirmationModalProps()
        if (activeMenu === 'Users') {
            onDeleteUser(data, setAddUserSideBlock)
        } else {
            onDeleteRole(data, setDetailsAndRolesShowBlock)
        }
    }

    const handleOpenConfirmationModalProps = (data) => {
        console.log(data)

        if (activeMenu === 'Users') {
            setConfirmationModalProps({
                ...confirmationModalProps,
                open: true,
                title: "Remove Selected User from this group?",
                message: `Are you sure you want to remove this user from this group?`,
                method: () => { handleDelete(data) }
            });
        } else {
            setConfirmationModalProps({
                ...confirmationModalProps,
                open: true,
                title: "Remove Selected Role from this group?",
                message: `Are you sure you want to remove this role from this group?`,
                method: () => { handleDelete(data) }
            });
        }

    }

    const handleCloseConfirmationModalProps = () => {
        setConfirmationModalProps({
            ...confirmationModalProps,
            open: false
        })
    };



    return (
        <>
            {/* 1ST ROW */}
            <Grid container className={classes.row}>
                <Grid item xs={2}>
                    <span className={classes.label}>Group ID</span>
                    <span className={classes.value}>{data.group.group_id}</span>
                </Grid>

                {props.mode === 'edit'
                ? ( 
                    <Grid item xs={4}>
                        <span className={classes.label}>Department</span>
                        <UI.Dropdown
                            placeholder="Select One"
                            icon={true}
                            error={dataTmp.department === ''}
                            isMulti={false}
                            closeOnSelect={true}
                            isCreatable={true}
                            value={selectedDepartment}
                            onChange={handleDepartment}
                            choices={departments}
                        />
                    </Grid>
                ) : null}

                {props.mode === 'edit'
                ? (
                    <Grid item xs={1}>
                    <Typography display="inline">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Typography>
                </Grid>
                ) : props.mode === 'view'
                ? (
                    <Grid item xs={2}>
                        <Typography display="inline">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Typography>
                    </Grid>
                ) : null}
                

                <Grid item xs={4}>
                    <span className={classes.label}>Description</span>
                    {props.mode === 'view'
                    ? (
                        <span className={classes.value}>{data.group.description}</span>
                    ) : props.mode === 'edit'
                    ? (
                        <BasicTextField
                            error={dataTmp.description === ''}
                            type="text"
                            value={dataTmp.description}
                            rows="3"
                            onChange={(e) => handleAll(e, 'description')}
                            labelFontSize="12px"
                        />
                    ) : null}
                    <Grid container>
                            {fieldValidation.description !== null && fieldValidation.description === false ?
                            <>
                                <Grid>
                            <span className={classesEdit.errorLabel}>{errorMessages.description}</span>
                                </Grid>
                                <Grid item xs={2} md={2} lg={2} style={{textAlign: 'right'}}>
                                    <ErrorIcon fontSize='small' className={classesEdit.errorIcon} />
                                </Grid>
                            </>    
                            : ''}
                    </Grid>
                </Grid>
            </Grid>

            {/* 2ND ROW */}
            <Grid container className={classes.row}>
                <Grid item xs={4}>
                    <span className={classes.label}>Group Name</span>
                    {props.mode === 'view'
                    ? (
                        <span className={classes.value}>{data.group.name}</span>
                    ) : props.mode ==='edit'
                    ? (
                        <TextField
                            type="text"
                            className={classesEdit.input}
                            id="outline-group-name"
                            variant="outlined"
                            fullWidth
                            value={data.group.name}
                            disabled
                            
                        />
                    ) : null
                    }
                </Grid>
                {props.mode === 'edit'
                ? (
                    <Grid item xs={3}>
                        <Typography display="inline">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Typography>
                    </Grid>
                ) : null}
                {props.mode === 'edit'
                ? (
                    <Grid item xs={4}>
                        <span className={classes.label}>Status</span>
                <span className={classes.value}>{data.group.status.toUpperCase()}</span>
                    </Grid>
                ) : null
                }
            </Grid>

            {/* 3RD ROW */}
            <Grid container className={classes.row}>
                <Grid item xs={4}>
                    <span className={classes.label}>Effectivity Date</span>
                    {props.mode === 'view'
                    ? (
                        <span className={classes.value}>{moment(data.group.effectivity_date).format('MMM DD, YYYY')}</span>
                    ) : props.mode === 'edit'
                    ? (
                        <Grid item>
                        <MuiPickersUtilsProvider utils={MomentUtils}>
                            <KeyboardDatePicker
                                allowKeyboardControl={false}
                                variant="dialog"
                                format="MMMM DD, YYYY"
                                inputVariant="outlined"
                                inputProps={{
                                    disabled: true
                                }}
                                value={selectedDate}
                                onChange={ handleEffectivityDateChange}
                                style={{width: "100%"}}
                            />
                        </MuiPickersUtilsProvider>
                        </Grid>
                    ) : null}
                    <Grid container>
                            {fieldValidation.effectivity_date !== null && fieldValidation.effectivity_date === false ?
                            <>
                                <Grid>
                            <span className={classesEdit.errorLabel}>{errorMessages.effectivity_date}</span>
                                </Grid>
                                <Grid item xs={2} md={2} lg={2} style={{textAlign: 'right'}}>
                                    <ErrorIcon fontSize='small' className={classesEdit.errorIcon} />
                                </Grid>
                            </>    
                            : ''}
                    </Grid>
                    
                </Grid>
                {props.mode === 'edit'
                ? (
                    <Grid item xs={3}>
                        <Typography display="inline">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Typography>
                    </Grid>
                ): null }
                <Grid item xs={4}>
                    <span className={classes.label}>Expiry Date</span>
                    {props.mode === 'view'
                    ? (
                        <span className={classes.value}>
                        {data.group.hasOwnProperty('expiry_date') ? moment(data.group.expiry_date).format('MMM DD, YYYY') : '-'}
                        </span>
                    ) : props.mode === 'edit'
                    ? (
                        <MuiPickersUtilsProvider utils={MomentUtils}>
                            <KeyboardDatePicker
                                allowKeyboardControl={false}
                                invalidDateMessage=""
                                disabled={selectedDate !== null ? false : true}
                                minDate={selectedDate !== null ? selectedDate : ''}
                                clearable
                                variant="dialog"
                                inputProps={{
                                    disabled: true
                                }}
                                format="MMMM DD, YYYY"
                                inputVariant="outlined"
                                value={selectedDate2}
                                onChange={handleExpireDateChange}
                                style={{ width: "100%" }}
                            />
                        </MuiPickersUtilsProvider>
                    ) : null}
                </Grid>
                {props.mode === 'view'
                ? (
                    <Grid item xs={4}>
                    <span className={classes.label}>Users in Group</span>
                    {/* <span className={classes.value}>{data.group.user_count}</span> */}
                    <span className={classes.value}>{data.group.user_count}</span>
                </Grid>
                ) : null}
                
            </Grid>
            {props.mode === 'edit'
            ? (
                <Grid container className={classes.row}>
                    <Grid item xs={4}>
                        <span className={classes.label}>Amount Limit</span>
                        <TextField
                            type="number"
                            className={classesEdit.input}
                            value={dataTmp.amount_limit}
                            onChange={numberChange}
                            fullWidth
                            id="outline-amountlimit"
                            variant="outlined"
                        />
                    </Grid>
                </Grid>
            ) : null}

            {/* Table section */}
            {props.mode === 'view'
            ? (
                <TableComponent
                rows={data.rows}
                columns={activeMenu === 'Users' ? columnsUser : columns}
                pageSizes={pageSizes}
                integrated={true}
                columnExtension={activeMenu === 'Users' ? COLUMN_EXTENSIONS_USERS : COLUMN_EXTENSIONS}
                buttonLabel={activeMenu === 'Users' ? 'Add to this group' : 'Add role'}
                handleClickAdd={handleClickAdd}
                activeMenu={activeMenu}
                onDeleteRow={(row) => {
                    handleOpenConfirmationModalProps(row)
                }}
            />
            ) : null}
            

            {/* Floating Footer for Edit Group */}
            {props.mode === 'edit'
            ? (
                <UI.FloatingFooter>
                    <Grid item xs={12} className={classesEdit.userAlignRight}>
                        <Button
                            className={classesEdit.cancelAdd}
                            variant="contained"
                            color="primary"
                            onClick={handleCancel}
                        >
                            Cancel
                        </Button>
                        <Button
                            className={classesEdit.saveButton}
                            disabled={Object.keys(dataChanges).length > 0 && dataChanges.constructor === Object && dataTmp.department !== '' && dataTmp.effectivity_date !== '' && dataTmp.description !== '' ? false : true}
                            variant="contained"
                            color="primary"
                            onClick={handleConfirmation}
                        >
                            Save Changes
                        </Button>
                    </Grid>
                </UI.FloatingFooter>
            ) : null}


            <SideModalBlock
                handleClose={() => { setDetailsAndRolesShowBlock('none') }}
                showBlock={activeMenu == 'Details and Roles' ? detailsAndRolesShowBlock : 'none'}
                title={'Add to this Group'}>
                <AddRoleGroupBody
                    onClose={() => {
                        setDetailsAndRolesShowBlock('none')
                    }}
                    onSave={(data: any) => {
                        onSaveRoleToGroup(data)
                        console.log(data)
                    }}
                />
            </SideModalBlock>


            <SideModalBlock
                title={'Add to this Group'}
                showBlock={activeMenu === 'Users' ? addUserSideBlock : 'none'}
                handleClose={() => setAddUserSideBlock('none')}>
                <AddUserGroupBody
                    onClose={() => {
                        setAddUserSideBlock('none')
                    }}
                    onSave={(data: any) => {
                        onSaveUserToGroup(data, setAddUserSideBlock)
                        console.log(data)
                    }}
                />
            </SideModalBlock>

            {/*confirmation modal for edit group*/}
            <ModalComponent
                    isModalOpen={cModalProps.open}
                    onClose={handleChangeMode}
                    message={cModalProps.message}
                    title={cModalProps.title}
                    buttons={
                        <Grid container justify="center" alignItems="center">
                            <Grid item xs={6}>
                    <Button onClick={cModalProps.methodLeft} className={classesEdit.button} variant={'outlined'} color={'secondary'}>{cModalProps.buttonLeft}</Button>
                            </Grid>
                            <Grid item xs={6}>
                    <Button onClick={cModalProps.methodRight} className={classesEdit.button} variant={'contained'} color={'primary'}>{cModalProps.buttonRight}</Button>
                            </Grid>
                        </Grid>
                    }
                />
            {/* {this modal is for confirmation for both removing groups and users} */}
            <ModalComponent
                isModalOpen={confirmationModalProps.open}
                onClose={handleCloseConfirmationModalProps}
                message={confirmationModalProps.message}
                title={confirmationModalProps.title}
                buttons={
                    <Grid container justify="center" alignItems="center">
                        <Grid item xs={6}>
                            <Button
                                onClick={handleCloseConfirmationModalProps}
                                className={classes.button}
                                variant={'outlined'}
                                color={'secondary'}>
                                Cancel
                            </Button>
                        </Grid>
                        <Grid item xs={6}>
                            <Button
                                onClick={confirmationModalProps.method}
                                className={classes.button}
                                variant={'contained'}
                                color={'primary'}>
                                Remove
                            </Button>
                        </Grid>
                    </Grid>
                }
            />





        </>
    )
}