import React, {
  useState,
} from 'react';
import {
  Grid,
  InputLabel,
  Typography,
  Button,
  Link,
  Checkbox,
  ListItemText,
  List,
  makeStyles
} from '@material-ui/core';
import moment from 'moment';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrashAlt, faMinusCircle } from '@fortawesome/free-solid-svg-icons';
import {
  GridHorizontalDivider,
} from 'Components/UI/';
import {
  BasicTextField,
  BasicSelect,
} from 'Components/UI/';
import {
  KeyboardDatePicker,
} from '@material-ui/pickers';
import { SIDE_MODAL_BLOCK_FIELDS } from '../FieldConstants'


const useStyles = makeStyles(() => ({
  keyboardDatePicker: {
    marginTop: '10px',
    marginBottom: '15px',
    width: '212px',
  },
  viewHeader: {
    color: '#272E4C',
    textAlign: 'left',
    opacity: 0.6,
    letterSpacing: 0,
    textAlignLast: 'left',
    fontSize: '12px',
  },
  viewHeaderValue: {
    color: '#272E4C',
    textAlign: 'left',
    opacity: 1,
    letterSpacing: 0,
    textAlignLast: 'left',
    fontSize: '14px',
    fontWeight: 600,
  },
  deleteButton: {
    textDecoration: 'underline',
    color: '#272E4C',
    opacity: 0.6,
    cursor: 'pointer',
  },
  checkbox: {
    '&.Mui-checked': {
      color: '#1e2071',
    },
  },
}));

interface SideModalBodyProps {
  formFields?: any;
  invalidFields?: any[];
  refreshData?: (data: any) => void;



  verticalSpacing?: string;
  deleteIcon?: string; // Text next to delete icon, unhides delete icon
  deactivateIcon?: string; // Text next to deactivate icon, unhides deactivate icon
  disableSaveButton?: boolean;



  delete?: (data: any) => void;
  deactivate?: (data: any) => void;
  
  
  
  handleClose: () => void;
  handleSave?: (formFields?: any) => void;



  mode: 'view' | 'edit' | 'add' | ''; // modes of side modal block 
  formName: string; // relates to FieldConstants.tsx. Set value to ICD10, CPT, or etc.




  dynamicDropdownChoices?: any[]; // for regular dropdown field
  checkBoxWithDropDownFieldsInitState?: boolean; // initial state of checkbox with 
  datePairField1MinDate?: Date | string; // min date for first picker of date pair fields
  datePairField1MaxDate?: Date | string; // max date for first picker of date pair fields
  datePairField2MinDate?: Date | string; // min date for second picker of date pair fields
  datePairField2MaxDate?: Date | string; // max date for second picker of date pair fields
}

export const SideModalBody = (props: SideModalBodyProps) => {

  const { formFields, handleClose, invalidFields, verticalSpacing } = props;
  const classes = useStyles();

  const [
    checkBoxWithDropDownChecked, 
    setcheckBoxWithDropDownChecked
  ] = useState(props.checkBoxWithDropDownFieldsInitState ? true : false);

  const {
    excludeFields,
    includeFields,
    regularTextBoxFields,
    regularDropDownFields,
    largeTextBoxFields,
    dateFields,
    datePairFields,
    checkBoxWithDropDownFields,
  } = SIDE_MODAL_BLOCK_FIELDS[props.formName][props.mode === 'add' || props.mode === 'edit' ? 'editOrAdd' : props.mode]




















  
















  /*****************************  
           FORM ACTIONS 
  ******************************/
  const handleFormChange = (data: any) => {
    if (props.refreshData) {
      props.refreshData(data)
    }
  }

  const handleDateChange = (date: any, form_fields: any, field: string) => {
    try {
      if (date && date.toDate() instanceof Date && !isNaN(date.toDate().valueOf())) {
        const iso_date = moment(date).toDate()
        handleFormChange(Object.assign(form_fields, { [field]: iso_date }))
      } else {
        handleFormChange(Object.assign(form_fields, { [field]: '' }))
      }
    } catch {
      handleFormChange(Object.assign(form_fields, { [field]: '' }))
    } finally {
    }
  };






































  /*****************************  
        LIST FOR VIEW MODE 
  ******************************/
  const renderViewItems = (field: string) => {
    const excluded_fields = Object.keys(excludeFields)
    const included_fields = Object.keys(includeFields)
    const date_fields = Object.keys(dateFields)

    if(props.formName && 
      ['ICD10', 'CPT'].includes(props.formName) &&
      field === 'gender' && 
      !formFields[field]){
        return null;
    }

    return (
      <div key={field + ' div'}>
        {!excluded_fields.includes(field) && included_fields.includes(field) ? (
          <ListItemText key={field + 'label'}>
            <div key={field + ' Header'} className={classes.viewHeader}>{includeFields[field].toUpperCase()}</div>
            <div key={field + ' Value'} className={classes.viewHeaderValue}>{formFields && formFields[field] && formFields[field] !== '' ? formFields[field] : '-'}</div>
            <br />
          </ListItemText>
        ) : null}
        {!excluded_fields.includes(field) && date_fields.includes(field) ? (
          <ListItemText key={field + 'date'}>
            <div key={field + ' Header'} className={classes.viewHeader}>{dateFields[field].toUpperCase()}</div>
            <div key={field + ' Value'} className={classes.viewHeaderValue}>{formFields && formFields[field] && formFields[field] !== '' ? moment(formFields[field]).format('LL') : '-'}</div>
            <br />
          </ListItemText>
        ) : null}
      </div>
    )
  }


  const renderViewMode = () => {
    const all_fields = Object.keys(formFields)
    return (
      <List>
        {all_fields.map(field => renderViewItems(field))}
      </List>
    )
  }



































  /*****************************  
    FORM FOR EDIT OR ADD MODE 
  ******************************/
  const renderRegularTextBoxFields = (field: string) => {
    return (
      <Grid item xs={12}>
        <BasicTextField
          labelFontSize="14px"
          id={field}
          label={regularTextBoxFields[field]}
          value={formFields && formFields[field] ? formFields[field] : ''}
          error={invalidFields && invalidFields.includes(field)}
          onChange={(e: any) => {
            handleFormChange(Object.assign(formFields, { [field]: e.target.value }))
          }}
        />
      </Grid>
    )
  }


  const renderLargeTextBoxFields = (field: string) => {
    return (
      <Grid item xs={12}>
        <BasicTextField
          labelFontSize="14px"
          id={field}
          rows="3"
          label={largeTextBoxFields[field]}
          value={formFields && formFields[field] ? formFields[field] : ''}
          error={invalidFields !== undefined && invalidFields.includes(field)}
          onChange={(e: any) => {
            handleFormChange(Object.assign(formFields, { [field]: e.target.value }))
          }}
        />
      </Grid>
    )
  }


  const renderDatePairFields = (field: string) => {
    const date_pair_keys : string[] = Object.keys(datePairFields)
    return (
      <Grid container>
        <Grid>
          <InputLabel htmlFor={field} style={{ fontSize: '14px' }}>
            {datePairFields[field]}
          </InputLabel>
          <KeyboardDatePicker
            allowKeyboardControl={false}
            id={field}
            variant="dialog"
            format="MM/DD/YYYY"
            inputVariant="outlined"
            maxDate={props.datePairField1MaxDate ? props.datePairField1MaxDate : undefined}
            minDate={props.datePairField1MinDate ? props.datePairField1MinDate : undefined}
            error={invalidFields !== undefined && invalidFields.includes(field)}
            className={classes.keyboardDatePicker}
            value={formFields ? moment(formFields[field]).format('MM-DD-YY').toString() : ''}
            invalidDateMessage=""
            onChange={date => handleDateChange(date, formFields, field)}
            />
        </Grid>
        <Typography display="inline">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Typography>
        <Grid>
          <InputLabel htmlFor={date_pair_keys[date_pair_keys.indexOf(field) + 1]} style={{ fontSize: '14px' }}>
            {datePairFields[date_pair_keys[date_pair_keys.indexOf(field) + 1]]}
          </InputLabel>
          <KeyboardDatePicker
            id={date_pair_keys[date_pair_keys.indexOf(field) + 1]}
            variant="dialog"
            format="MM/DD/YYYY"
            inputVariant="outlined"
            minDate={props.datePairField2MinDate ? props.datePairField2MinDate : undefined}
            maxDate={props.datePairField2MaxDate ? props.datePairField2MaxDate : undefined}
            error={invalidFields && invalidFields.includes(Object.keys(datePairFields)[Object.keys(datePairFields).indexOf(field) + 1])}
            className={classes.keyboardDatePicker}
            value={formFields && formFields[date_pair_keys[date_pair_keys.indexOf(field) + 1]] ?
                   moment(formFields[date_pair_keys[date_pair_keys.indexOf(field) + 1]]).format('MM/DD/YY').toString() : ''}
            invalidDateMessage=""
            onChange={date => {
              handleDateChange(date, formFields, Object.keys(datePairFields)[Object.keys(datePairFields).indexOf(field) + 1])
            }}
          />
        </Grid>
      </Grid>
    )
  }


  const renderDateFields = (field: string) => {
    return (
      <Grid item xs={12}>
        <InputLabel htmlFor={field} style={{ fontSize: '14px' }}>{dateFields[field]}</InputLabel>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <KeyboardDatePicker
            id={field}
            variant="dialog"
            format="MM/DD/YYYY"
            inputVariant="outlined"
            className={classes.keyboardDatePicker}
            error={invalidFields && invalidFields.includes(field)}
            value={formFields && formFields[field] ? moment(formFields[field]).format('MM-DD-YY').toString() : ''}
            invalidDateMessage=""
            onChange={date => handleDateChange(date, formFields, field)}
          />
        </div>
      </Grid>
    )
  }


  const renderCheckboxWithDropdownFields = (field: string) => {
    return (
      <Grid item xs={12}>
        <Checkbox style={{ marginTop: '-10px', marginLeft: '-10px', marginBottom: '-1px' }}
          color="primary"
          classes={{colorPrimary: classes.checkbox,}}
          checked={checkBoxWithDropDownChecked}
          onChange={(event: React.ChangeEvent<HTMLInputElement>, ) => {
            setcheckBoxWithDropDownChecked(event.target.checked);
            handleFormChange(Object.assign(formFields, { [checkBoxWithDropDownFields[checkBoxWithDropDownFields.indexOf(field) + 2]]: event.target.checked }))
          }}
        /> 
        {checkBoxWithDropDownFields[checkBoxWithDropDownFields.indexOf(field) + 3]}
        {checkBoxWithDropDownChecked ? (
          <React.Fragment>
            <BasicSelect
              marginTop='10px'
              marginBottom='10px'
              labelFontSize='14px'
              id={checkBoxWithDropDownFields[checkBoxWithDropDownFields.indexOf(field) + 1]}
              label={checkBoxWithDropDownFields[checkBoxWithDropDownFields.indexOf(field) + 1]}
              error={invalidFields !== undefined && invalidFields !== undefined && invalidFields.includes(checkBoxWithDropDownFields[0])}
              menuItems={checkBoxWithDropDownFields[checkBoxWithDropDownFields.indexOf(field) + 4]}
              value={formFields && formFields[field] ? formFields[field] : '-'}
              onChange={(e: any) => handleFormChange(Object.assign(formFields, { [field]: e.target.value }))}
              width="230px"
            />
          </React.Fragment>
            ) : null}
      </Grid>
    )
  }


  const renderRegularDropdownFields = (field: string) => {
    return (
      <React.Fragment>
        <BasicSelect
          marginTop='10px'
          marginBottom='10px'
          labelFontSize='14px'
          id={field}
          label={regularDropDownFields[field]}
          error={invalidFields && invalidFields.includes(field)}
          menuItems={props.dynamicDropdownChoices ? props.dynamicDropdownChoices : []}
          value={formFields && formFields[field] ? formFields[field] : '-'}
          onChange={(e: any) => handleFormChange(Object.assign(formFields, { [field]: e.target.value }))}
          width="100%"
          maxWidth="100%"
        />
      </React.Fragment>
    )
  }


  const renderEditOrAddForm = (field: string) => {
    const exclude_fields: string[] = excludeFields ? Object.keys(excludeFields) : []
    if (exclude_fields.includes(field)) return null;

    const renderRegularTextBoxes: boolean = regularTextBoxFields &&
      Object.keys(regularTextBoxFields).includes(field) ?
      true : false;

    const renderLargeTextBoxes: boolean = largeTextBoxFields &&
      Object.keys(largeTextBoxFields).includes(field) ?
      true : false;

    const renderDatePairPickers: boolean = datePairFields &&
      Object.keys(datePairFields).includes(field) && Object.keys(datePairFields).indexOf(field) % 2 === 0 ?
      true : false;

    const renderDatePickers: boolean = dateFields &&
      Object.keys(dateFields).includes(field) ?
      true : false;

    const renderCheckBoxWithDropDown: boolean = checkBoxWithDropDownFields &&
      checkBoxWithDropDownFields.includes(field) && 
      checkBoxWithDropDownFields.indexOf(field) % 5 === 0 ?
      true : false;

    const renderRegularDropDown: boolean = regularDropDownFields &&
      Object.keys(regularDropDownFields).includes(field) ?
      true : false;

    return (
      <div key={`block-body-${field}`}>
        {renderRegularTextBoxes ? renderRegularTextBoxFields(field) : null}
        {renderLargeTextBoxes ? renderLargeTextBoxFields(field) : null}
        {renderDatePairPickers ? renderDatePairFields(field) : null}
        {renderDatePickers ? renderDateFields(field) : null}
        {renderCheckBoxWithDropDown ? renderCheckboxWithDropdownFields(field) : null}
        {renderRegularDropDown ? renderRegularDropdownFields(field) : null}
        <GridHorizontalDivider height={verticalSpacing ? verticalSpacing : "0px"} />
      </div>
    )
  }

  const renderEditOrAddMode = () => {
    const all_fields = Object.keys(formFields)
    return (
      <>
        {all_fields.map(field => renderEditOrAddForm(field))}
      </>
    )
  }



































  /*****************************  
           MAIN RENDER 
  ******************************/
  return (
    <div style={{ maxWidth: '450px' }}>
      {props.mode === 'view' ?
          renderViewMode()
        : props.mode === 'edit' || props.mode === 'add' ? (
          renderEditOrAddMode()
        ) : null}

      {props.mode === 'edit' && props.deleteIcon && props.deleteIcon !== '' ? (
        <Link className={classes.deleteButton} style={{ marginTop: '-10px', marginBottom: '-10px' }}
          onClick={() => {
            if (props.delete) {
              props.delete(formFields)
            }
        }}>
          <FontAwesomeIcon icon={faTrashAlt} style={{ marginRight: '10px' }} />
          {props.deleteIcon}
        </Link>
      ) : null}

      {props.mode === 'edit' && props.deactivateIcon && props.deactivateIcon !== '' ? (
        <Link
          className={classes.deleteButton}
          onClick={() => {
            if (props.deactivate) {
              props.deactivate(formFields)
            }
          }} style={{ marginTop: '-10px', marginBottom: '-10px' }}>
          <FontAwesomeIcon icon={faMinusCircle} style={{ marginRight: '3px', textDecoration: 'underlined' }} />
          {props.deactivateIcon}
        </Link>
      ) : null}

      <GridHorizontalDivider height="10px" />
      {props.mode === 'edit' || props.mode === 'add' ? (
        <Grid container spacing={2} justify="center" alignItems="flex-start">
          <Grid item xs={4}>
            <Button onClick={handleClose} variant="outlined" color="default" fullWidth>Cancel</Button>
          </Grid>
          <Grid item xs={4}>
            <Button
              onClick={
                () => {
                  if (props.handleSave) 
                    props.handleSave(formFields);
                }}
              variant="contained"
              color="primary"
              fullWidth
              disabled={props.disableSaveButton ? props.disableSaveButton : false}
            >
              Save
          </Button>
          </Grid>
        </Grid>
      ) : null}
    </div>
  );
};

export default SideModalBody;
