import Fetch, { requestOption } from '../index';

const fetch = new Fetch();

export const getGroupList = (
    limit: number,
    offset?: number,
    skip?: number,
    filter?: any,
    order?: any
) => {
    let option: requestOption = {
        method: 'Get'
    }
    console.log(filter);

    return fetch.call(option, `account-groups?${filter}filter[limit]=${limit}&filter[skip]=${skip}&filter[offset]=${offset}${order}`)
}

export const getGroups = (
    filter: any,
    where?: any,
    order?: any,
    query?: string
) => {
    let option: requestOption = {
        method: 'GET'
    }

    if (order && order !== '') {
        filter.order = order;
    }

    if (where && Object.keys(where).length > 0) {
        filter.where = where;
    }
    let params = {
        filter: JSON.stringify(filter)
    }

    if (query && query !== '') {
        params['query'] = query;
    }

    return fetch.call(option, `group/list?${new URLSearchParams(params).toString()}`)
}

export const addNewGroup = (data: any) => {
    let option: requestOption = {
        method: 'POST',
        body: JSON.stringify(data)
    }

    return fetch.fetchAccessToken().then(() =>
        fetch.call(option, `group`, true))
}

export const patchRoleToGroup = (role_id, data: any) => {
    let option: requestOption = {
        method: 'PATCH',
        body: JSON.stringify(data)
    }

    return fetch.fetchAccessToken().then(() => fetch.call(option, `group/role/${role_id}`, true))
}

