export const getAllRoles = (filter: any, where?: any) => {

    let option: requestOption = {
        method: 'GET'
    }

    if (where && where !== '') {
        filter.where = where
    }

    let params = {
        filter: JSON.stringify(filter)
    }

    return fetch.fetchAccessToken().then(() => fetch.call(option, `roles?${new URLSearchParams(params).toString()}`, true))
}

