// DECLARE ALL COOMON CONST

const regex = {
    email: new RegExp(/\S+@\S+\.\S+/),
    username: new RegExp(/^[A-Za-z_][A-Za-z\d_-]*$/),
    emp_id: new RegExp(/^[a-z0-9_-]*/i)
}

const getLastSegment = () => {
    return window.location.href.substring(window.location.href.lastIndexOf('/') + 1)
}

export {
    regex,
    getLastSegment
}