import * as React from 'react';
import { Grid } from '@material-ui/core';
// import { GridHorizontalDivider } from 'Components/UI';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDownload } from '@fortawesome/free-solid-svg-icons';
import Link from '@material-ui/core/Link';
import { makeStyles } from '@material-ui/core/styles';
import { UI } from 'Components'

export interface IGenerateReportProps {
}

const useStyles = makeStyles({
    addFieldIcon: {
        color: '#3AB77D',
        cursor: 'pointer',
        fontSize: '13px',
        marginRight: '8px',
    },
    linkStyleSelected: {
        '&:hover': {
            fontWeight: 'normal'
        }
    }
});



export const GenerateReport: React.FC<IGenerateReportProps> = (props: IGenerateReportProps): JSX.Element => {
    const { } = props;
    const classes = useStyles();
    const [anchorEl,setAnchorEl] = React.useState(null);

    const handleClick = event => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const open = Boolean(anchorEl);
    return (
        <Grid item>

            {/* <GridHorizontalDivider height="25px" /> */}
            <FontAwesomeIcon icon={faDownload} className={classes.addFieldIcon} />
            <Link onClick={handleClick} className={classes.linkStyleSelected}>Generate Report</Link>


            <UI.ReportPopover
                open={open}
                anchorEl={anchorEl}
                exportData=''
                exportFormat=''
                onClose={handleClose}
                onChangeExportData={{}}
                onChangeExportFormat={{}}
                generateReport={{}} />

        </Grid>
    )

}