import React from 'react';

import Select from 'react-select';
import AsyncSelect from 'react-select/async';
import CreatableSelect from 'react-select/creatable';

interface IProps {
    choices: any[]
    onChange: (value) => void
    fetchOption?: (value, cb) => void
    value: any
    isMulti: boolean
    closeOnSelect: boolean
    icon: boolean,
    placeholder: string,
    async?: boolean,
    error?: boolean,
    disabled?: boolean,
    defaultValue?: any
    isCreatable?: boolean
}

export const Dropdown: React.FC<IProps> = (props: IProps):JSX.Element => {
    const {
        choices,
        onChange,
        value,
        isMulti,
        closeOnSelect,
        icon,
        placeholder,
        async,
        fetchOption,
        error,
        disabled,
        isCreatable
    } = props;

    const multiValueStyles = {
        control: (base) => {
            return {
                ...base,
                borderColor: error ? '#E53935' : '#272E4C4D'
            }
        },
        multiValue: (styles) => {
            return {
                ...styles,
                borderRadius: '25px'
            };
        }
    };

    const loadOptions = (inputValue, callback) => {
        if (typingTimeout) {
            clearTimeout(typingTimeout);
        }

        setTypingTimeout (setTimeout(() => {
            fetchOption && fetchOption(inputValue, callback)
        }, 1000))
    };
    

    const [typingTimeout, setTypingTimeout] = React.useState<any>(0)
    return (
        <>
            {
                async && fetchOption ? 
                <AsyncSelect
                    isLoading={value && value.length > 0 ? true : false}
                    isDisabled={disabled}
                    onChange={onChange}
                    cacheOptions
                    defaultOptions={choices}
                    loadOptions={loadOptions}
                    placeholder={placeholder}
                    value={value}
                    isMulti={isMulti}
                    closeMenuOnSelect={closeOnSelect}
                    components={!icon ? { DropdownIndicator:() => null, IndicatorSeparator:() => null } : {}} />
                : isCreatable && isCreatable ? 
                    <CreatableSelect
                        className="basic-single error"
                        classNamePrefix="select"
                        value={value instanceof Object && Object.entries(value).length <= 0 ? '' : value}
                        isSearchable={true}
                        name="color"
                        options={choices}
                        onChange={onChange}
                        placeholder={placeholder}
                        isMulti={isMulti}
                        closeMenuOnSelect={closeOnSelect}
                        components={!icon ? { DropdownIndicator:() => null, IndicatorSeparator:() => null } : {}}
                        styles={multiValueStyles}
                    />
                :
                <Select
                    className="basic-single error"
                    classNamePrefix="select"
                    value={value instanceof Object && Object.entries(value).length <= 0 ? '' : value}
                    isSearchable={true}
                    name="color"
                    options={choices}
                    onChange={onChange}
                    placeholder={placeholder}
                    isMulti={isMulti}
                    closeMenuOnSelect={closeOnSelect}
                    components={!icon ? { DropdownIndicator:() => null, IndicatorSeparator:() => null } : {}}
                    styles={multiValueStyles}
                />
            }
        </>
    )
}