import * as React from 'react';
import { SearchUserGroup } from 'Components/UI/SearchBar/SearchUserGroup';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimesCircle } from '@fortawesome/free-solid-svg-icons';
// import { InputLabel } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Button, Grid } from '@material-ui/core';
import { getAllRoles } from 'Pages/API/Calls/Group';

const useStyles = makeStyles(() => ({
    selectedItems: {
        borderRadius: 20,
        fontSize: 13,
        backgroundColor: '#d1d4db',
        padding: '5px 10px'
    },
    xIcon: {
        fontSize: 16
    }
}));
console.log(useStyles);

interface IAddRoleGroupBodyProps {
    onClose: () => void;
    onSave: (items) => void;
}

interface IAddRoleGroupBodyState {
    selectedItems: any;
    comboArray: any[];
}


export class AddRoleGroupBody extends React.Component<IAddRoleGroupBodyProps, IAddRoleGroupBodyState> {
    constructor(props) {
        super(props);
        this.state = {
            selectedItems: [],
            comboArray: [],
        }
    }

    componentDidMount() {
        let filter = {
            limit: 500
        }

        let where = {
            status: "active"
        }

        getAllRoles(filter, where).then(res => {
            console.log("all roles active")
            console.log(res)
            let data = {
                body: res.body,
            }
            let newChoices: any = [];

            if (data.body && data.body.length > 0) {
                data.body.map(c => {
                    let beauty = {
                        id: c["_id"],
                        name: c["name"],
                        description: c["description"],
                        module: c["module"],
                        role_id: c["role_id"],
                        is_processmaker: c["is_processmaker"],
                        effectivity_date: c["effectivity_date"],
                        rbac: c["rbac"],
                        associated_group: c["associated_group"],
                        status: c["status"],
                    }
                    newChoices.push(beauty)
                })
                this.setState({
                    comboArray: newChoices
                })
                console.log(this.state.comboArray)
            }
        }).catch(err => {
            console.log(err);
        })
    }


    handleOnSearch = (list) => {
        console.log(list)
        if (!list || typeof list !== 'object' || (list && !list.name)) return;
        let arr = this.state.selectedItems;
        arr.push(list);
        this.setState({
            selectedItems: arr
        })
        console.log(arr);
    }

    handleRemoveItem = (index) => {
        let arr = this.state.selectedItems;
        arr.splice(index, 1);
        this.setState({
            selectedItems: arr
        })
    }

    handleOnSaveItems = () => {
        this.props.onSave(this.state.selectedItems);
        this.setState({
            selectedItems: []
        })
    }

    public render(): JSX.Element {
        const classes = {
            buttonsContainer: {
                padding: '1em',
            },
            selectedContainer: {
                padding: '3em 0 2em'
            },
            selectedItems: {
                borderRadius: 20,
                fontSize: 13,
                backgroundColor: '#d1d4db',
                padding: '5px 10px',
                margin: '5px',
                display: 'inline-block',
            },
            selectedText: {
                paddingRight: '5px'
            },
            saveButton: {
                width: '135px',
                fontSize: '14px',
                color: '#fff',
            },
            cancelButton: {
                width: '135px',
                fontSize: '14px',
            }
        };

        return (
            <>
                <div style={{ minHeight: 400, minWidth: 400 }}>
                    <SearchUserGroup
                        placeholder={'Please enter at least 3 characters to search.'}
                        searchLabel={'Search user roles'}
                        onSearch={this.handleOnSearch}
                        comboBoxMode={true}
                        comboListsArray={this.state.comboArray}
                    />
                    <div style={classes.selectedContainer}>
                        {this.state.selectedItems.length > 0 ?
                            <div>
                                <label>Selected Roles</label>
                            </div>
                            : null}
                        {this.state.selectedItems.map((item, index) =>
                            <div style={classes.selectedItems}>
                                <span style={classes.selectedText}>{item.name}</span>
                                <span onClick={() => this.handleRemoveItem(index)}>
                                    <FontAwesomeIcon icon={faTimesCircle} />
                                </span>
                            </div>
                        )
                        }
                    </div>
                </div>
                <Grid container spacing={1}>
                    <Grid item xs={6}>
                        <Button style={classes.cancelButton} variant="outlined" color="secondary" onClick={() => this.props.onClose()}>Cancel</Button>
                    </Grid>
                    <Grid item xs={6}>
                        <Button disabled={this.state.selectedItems.length < 1 ? true : false}
                            style={classes.saveButton}
                            variant="contained"
                            color="primary"
                            onClick={() => this.handleOnSaveItems()}>
                            Add
                        </Button>
                    </Grid>
                </Grid>
            </>
        )
    }

}
