import React from 'react'
import moment from 'moment'

// Material UI imports
import {
    Grid,
    Button
} from '@material-ui/core'

// Relative Imports
import { ViewGroup } from './ViewGroup/ViewGroup'
import { SubHeader } from 'Components/UI/SubHeader'
import { SideMenu } from 'Components/UI/SideMenu'
import { sidebarItems } from './Group.const'
import { GroupStyles } from './styles'
import { getLastSegment } from 'Components/app.const'
import { UI } from 'Components'
import { patchRoleToGroup } from 'Pages/API/Calls/Group';
import { patchUserToGroup } from 'Pages/API/Calls/Group';
import { detachRoleToGroup } from 'Pages/API/Calls/Group';
import { detachUserToGroup } from 'Pages/API/Calls/Group';
import { ModalComponent } from 'Components/UI/ModalComponent/ModalComponent';

// API imports
import { getGroup, getUserWithoutGroup } from 'Pages/API/Calls/User'
import { updateGroup } from 'Pages/API/Calls/Group';


export const Group: React.FC = (): JSX.Element => {

    const classes = GroupStyles()
    /******* States for Edit Group *******/
    const [groupMode, setGroupMode] = React.useState<string>('view')
    const [dataTmp, setDataTmp] = React.useState<any>({
        description: '',
        department: '',
        effectivity_date: '',
        expiry_date: '',
        amount_limit: 0
    })
    /******* End of States for Edit Group *******/
    const [activeMenu, setActiveMenu] = React.useState<string>(sidebarItems[0].name)
    
    React.useEffect(() => {
        setData({
            ...data,
            rows: []
        })

        setIsLoading(true)
        if (activeMenu === 'Users') {
            getUserWithoutGroup(getLastSegment()).then((res) => {
                if (!res.error) {
                    let modData = res.body.map((e: any) => {
                        let newObj = {
                            ...e,
                            last_login: e['last_login'] ? moment(e.last_login).format('MMM DD, YYYY') : 'N/A',
                            user_status: e.status
                        }

                        return newObj
                    })
                    console.log(modData)

                    setData({
                        ...data,
                        rows: modData
                    })
                } else {
                    console.log(res.error.message)
                }
                setIsLoading(false)
            }).catch((err) => {
                console.log(err)
                setIsLoading(false)
            })
        } else {
            getGroup(getLastSegment()).then((r) => {
                if (!r.error) {
                    let modData = r.body[0].roles.map((e: any) => {
                        let newObj = {
                            ...e,
                            role_status: e.status
                        }

                        return newObj
                    })
                    console.log(modData)

                    setData({
                        ...data,
                        rows: modData,
                        group: r.body[0]
                    })
                    setDataTmp(r.body[0])


                    if (breadCrumbsArray.length < 2) {
                        let newBreadCrumb = breadCrumbsArray
                        newBreadCrumb.push({
                            name: r.body[0].name
                        })
                        setBreadCrumbs(newBreadCrumb)
                    }
                } else {
                    console.log(r.error.message);
                }

                setIsLoading(false)
            }).catch((err) => {
                console.log(err.message)
                setIsLoading(false)
            })
        }
        
    }, [activeMenu])

    const editMode = () => {
        setGroupMode('edit')
    }

    const makeActive = (value) => {
        setActiveMenu(value);
    }
    console.log(`This is the data `, dataTmp)

    const [isLoading, setIsLoading] = React.useState<boolean>(false)
    const [data, setData] = React.useState<any>({
        rows: [],
        group: {}
    })
    const [breadCrumbsArray, setBreadCrumbs] = React.useState<any[]>([
        {
            name: 'GROUPS',
            link: "/system-admin/users/groups"
        }
    ])
    const [modalProps, setModalProps] = React.useState({
        title: '',
        message: '',
        open: false,
        method: () => { }
    });

    const handleSaveUserToGroup = (items, cb) => {
        setIsLoading(true)
        let user = {
            body: [items]
        }
        console.log(user.body);

        let payload: any = [];

        if (user.body && user.body.length > 0) {
            user.body[0].map(c => {
                let selectedUserToAttach = {
                    _id: c['_id'],
                    last_name: c['last_name'],
                    middle_name: c['middle_name'],
                    first_name: c['first_name'],
                    job_title: c['job_title'],
                    username: c['username'],
                    department: c['department'],
                    effectivity_date: c['effectivity_date'],
                    email: c['email'],
                    employee_id: c['employee_id'],
                    is_active: c['is_active'],
                    location: c['location'],
                    main_module: c['main_module'],
                    main_role: c['main_role'],
                    pmaker_uid: c['pmaker_uid'],
                    profile_pic: c['profile_pic'],
                    roles: c['roles'],
                    status: c['status']
                }
                payload.push(selectedUserToAttach)
            })
        }

        patchUserToGroup(data.group["_id"], payload).then((resp: any) => {
            setIsLoading(false)
            if (!resp.error) {
                setModalProps({
                    ...modalProps,
                    open: true,
                    title: 'New user added!',
                    message: 'New user have been successfully added.',
                    method: handleCloseModalProps
                })

                let modData = items.map(e => {
                    let newObj = {
                        ...e,
                        last_login: e['last_login'] ? moment(e.last_login).format('MMM DD, YYYY') : 'N/A',
                        user_status: e.status
                    }

                    return newObj
                })

                cb('none')
                console.log("new", [...data.rows, ...modData])
                setData({
                    ...data,
                    group: resp,
                    rows: [...data.rows, ...modData]
                })


            } else {
                setModalProps({
                    ...modalProps,
                    open: true,
                    title: 'Add User Failed!',
                    message: 'Something went wrong. New user wasn’t added.',
                    method: handleCloseModalProps
                })
            }
        }).catch((err) => {
            setIsLoading(false)
            setModalProps({
                ...modalProps,
                open: true,
                title: 'Add User Failed!',
                message: err.message,
                method: handleCloseModalProps
            })
            console.log(err)
        })
    }

    const handleSaveRolesToGroup = (items, cb) => {

        ///CONSTRUCT PAYLOAD///

        setIsLoading(true)
        let roles = {
            body: [items],
            label: "name"
        }
        console.log("rolebody")
        console.log(roles.body)

        let payload: any = [];

        if (roles.body && roles.body.length > 0) {
            roles.body[0].map(a => {
                let selectedRoleToAttach = {
                    _id: a["id"],
                    name: a["name"],
                    description: a["description"],
                    module: a["module"],
                    role_id: a["role_id"],
                    is_processmaker: a["is_processmaker"],
                    effectivity_date: a["effectivity_date"],
                    rbac: a["rbac"],
                    status: a["status"]
                }
                let associated_groups = a["associated_group"]
                let newRolesToAttach: any = [];

                if (associated_groups && associated_groups.length > 0) {

                    associated_groups.map(e => {
                        var role_id = e["_id"];
                        if (role_id !== data.group["_id"]) {
                            newRolesToAttach.push(e)
                        }
                    })
                } else {
                    newRolesToAttach.push(data.group)
                }

                selectedRoleToAttach["associated_group"] = newRolesToAttach;
                payload.push(selectedRoleToAttach)
            })
        }

        patchRoleToGroup(data.group["_id"], payload).then((resp: any) => {
            setIsLoading(false)
            console.log(resp)
            if (!resp.error) {
                setModalProps({
                    ...modalProps,
                    open: true,
                    title: 'New Role added!',
                    message: 'New user role have been successfully added.',
                    method: handleCloseModalProps
                })

                let response = {
                    body: resp.roles
                }

                let newData: any = [];
                response.body.map(b => {
                    let roles = {
                        _id: b["_id"],
                        name: b["name"],
                        description: b["description"],
                        module: b["module"],
                        role_id: b["role_id"],
                        is_processmaker: b["is_processmaker"],
                        effectivity_date: b["effectivity_date"],
                        rbac: b["rbac"],
                        associated_group: b["associated_group"],
                        role_status: b["status"]
                    }
                    newData.push(roles)
                })
                console.log(newData)
                cb('none')
                setData({
                    ...data,
                    rows: newData
                })


            } else {
                setModalProps({
                    ...modalProps,
                    open: true,
                    title: 'Add User Role Failed!',
                    message: 'Something went wrong. New user role wasn’t added.',
                    method: handleCloseModalProps
                })
            }
        }).catch((err) => {
            setIsLoading(false)
            setModalProps({
                ...modalProps,
                open: true,
                title: 'Add User Role Failed!',
                message: err.message,
                method: handleCloseModalProps
            })
            console.log(err)
        })

    }

    /*****************************
        Handles for Edit Group
    ******************************/

    const handleOnSaveUpdateGroup = (groupData) => {
        setIsLoading(true)
        console.log(`this is the new data`, groupData)
        let fuse = Object.assign(groupData, data.group);

        setDataTmp(fuse)

        if (!dataTmp.expiry_date) {
            delete dataTmp.expiry_date
        }

        if(!dataTmp.amount_limit) {
            delete dataTmp.amount_limit
        }

        updateGroup(data.group["_id"], dataTmp)
        .then((resp: any) => {
            console.log(`this is the response: `, resp)
            setIsLoading(false)
            if(!resp.error) {
                setModalProps({
                    ...modalProps,
                    open: true,
                    title: 'Changes Saved',
                    message: 'Changes to this group have been saved successfully!',
                    method: handleSuccessUpdate
                })
                setData({
                    ...data,
                    group: resp
                })
                setDataTmp(resp)
            }
        })
        .catch(() => {
            setIsLoading(false)
            setModalProps({
                ...modalProps,
                open: true,
                title: 'Error Saving Changes',
                message: 'An error occured. Changes on this group is not saved. Please try again!',
                method: handleCloseModalProps
            })
        })
    }

    const handleSuccessUpdate = () => {
        setModalProps({
            ...modalProps,
            open: false
        })
        setIsLoading(true)
        setGroupMode('view')
        setIsLoading(false)
    }
    const handleDeleteUser = (item, cb) => {
        setIsLoading(true)
        console.log(data)
        console.log(cb)

        let user = {
            body: [item]
        }

        let payload: any = [];

        if (user.body && user.body.length > 0) {
            user.body.map(c => {
                let selectedUserToAttach = {
                    _id: c['_id'],
                    last_name: c['last_name'],
                    middle_name: c['middle_name'],
                    first_name: c['first_name'],
                    job_title: c['job_title'],
                    username: c['username'],
                    department: c['department'],
                    effectivity_date: c['effectivity_date'],
                    email: c['email'],
                    employee_id: c['employee_id'],
                    is_active: c['is_active'],
                    location: c['location'],
                    main_module: c['main_module'],
                    main_role: c['main_role'],
                    pmaker_uid: c['pmaker_uid'],
                    profile_pic: c['profile_pic'],
                    roles: c['roles'],
                    status: c['role_status']
                }
                payload.push(selectedUserToAttach)
            })
        }
        console.log("user payload")
        console.log(payload)
        console.log(JSON.stringify(payload));
        console.log(data.group["_id"])

        detachUserToGroup(data.group["_id"], payload).then((resp: any) => {
            setIsLoading(false)
            console.log("response")
            console.log(resp)
            if (resp !== null) {
                setModalProps({
                    ...modalProps,
                    open: true,
                    title: 'Successfully Removed from List!',
                    message: 'User has been succesfully removed from the list',
                    method: handleCloseModalProps
                })

                let newRows = data.rows;

                let removeIndex = newRows.map(function (el) { return el._id; }).indexOf(item._id);
                newRows.splice(removeIndex, 1);

                cb('none')
                setData({
                    ...data,
                    group: resp,
                    rows: newRows
                })


            } else {
                setModalProps({
                    ...modalProps,
                    open: true,
                    title: 'Failed Removing from List!',
                    message: 'Something went wrong. Group User wasn’t removed.',
                    method: handleCloseModalProps
                })
            }
        }).catch((err) => {
            setIsLoading(false)
            console.log("catch")
            setModalProps({
                ...modalProps,
                open: true,
                title: 'Failed Removing from List!',
                message: 'Something went wrong. Group User wasn’t removed.',
                method: handleCloseModalProps
            })
            console.log(err)
        })

    }

    const handleDeleteRole = (item: any[], cb) => {
        setIsLoading(true)
        console.log("ITEM")
        console.log(item)
        // console.log(cb)

        let newRoles = {
            body: [item]
        }
        console.log("newRoles")
        console.log(newRoles)
        let payload: any = [];

        if (newRoles.body && newRoles.body.length > 0) {
            newRoles.body.map(a => {
                console.log("nakakapasok ba?")
                let selectedRoleToAttach = {
                    _id: a["_id"],
                    name: a["name"],
                    description: a["description"],
                    module: a["module"],
                    role_id: a["role_id"],
                    is_processmaker: a["is_processmaker"],
                    effectivity_date: a["effectivity_date"],
                    rbac: a["rbac"],
                    status: a["role_status"],
                    associated_group: a["associated_group"]
                }
                payload.push(selectedRoleToAttach)
            })
        }
        console.log("Role payload")
        console.log(payload)

        detachRoleToGroup(data.group["_id"], payload).then((resp: any) => {
            setIsLoading(false)
            console.log(resp)
            if (!resp.error) {
                setModalProps({
                    ...modalProps,
                    open: true,
                    title: 'Successfully Removed from List!',
                    message: 'Role has been succesfully removed from the list',
                    method: handleCloseModalProps
                })

                let response = {
                    body: resp.roles
                }

                let newData: any = [];
                response.body.map(b => {
                    let roles = {
                        _id: b["_id"],
                        name: b["name"],
                        description: b["description"],
                        module: b["module"],
                        role_id: b["role_id"],
                        is_processmaker: b["is_processmaker"],
                        effectivity_date: b["effectivity_date"],
                        rbac: b["rbac"],
                        associated_group: b["associated_group"],
                        role_status: b["status"]
                    }
                    newData.push(roles)
                })
                console.log(newData)
                cb('none')
                setData({
                    ...data,
                    rows: newData
                })


            } else {
                setModalProps({
                    ...modalProps,
                    open: true,
                    title: 'Failed Removing from List!',
                    message: 'Something went wrong. Group Role wasn’t removed.',
                    method: handleCloseModalProps
                })
            }
        }).catch((err) => {
            setIsLoading(false)
            setModalProps({
                ...modalProps,
                open: true,
                title: 'Failed Removing from List!',
                message: 'Something went wrong. Group Role wasn’t removed.',
                method: handleCloseModalProps
            })
            console.log(err)
        })

    }

    const handleCloseModalProps = () => {
        setModalProps({
            ...modalProps,
            open: false
        })
    };


    return (
        <>
            {isLoading && <UI.LoadingIndicator />}
            <SubHeader
                headingTitle={'Sales'}
                breadCrumbsArray={breadCrumbsArray}
                buttonFunc={editMode}
                buttonName={'Edit Group'}
                layoutType={'layout_1'}
                enableButton={groupMode === 'edit' ? false : true} />

            <div className={classes.main}>
                <Grid container xs={12}>
                    <Grid item xs={3} className={classes.sideBarContainer}>
                        <SideMenu
                            items={sidebarItems}
                            activeItems={makeActive}
                            setActiveItem={0} />
                    </Grid>
                    <Grid item xs={9} className={classes.contentContainer}> 
                        <ViewGroup
                            mode={groupMode}
                            dataTmp={dataTmp}
                            setDataTmp={setDataTmp}
                            onSaveRolesToGroup={(data: any, cb) => {
                                handleSaveRolesToGroup(data, cb)
                            }}
                            onSaveUserToGroup={(data: any, cb) => {
                                handleSaveUserToGroup(data, cb)
                            }}
                            onDeleteRole={(data: any, cb) => {
                                handleDeleteRole(data, cb)
                            }}
                            onDeleteUser={(data: any, cb) => {
                                handleDeleteUser(data, cb)
                            }}
                            data={data}
                            onSaveGroupUpdate={(groupData: any) => {handleOnSaveUpdateGroup(groupData)}}
                            setIsLoading={setIsLoading}
                            
                            activeMenu={activeMenu}
                            setGroupMode={setGroupMode}
                            />
                    </Grid>
                </Grid>
            </div>

            {/* {this modal is for success and error for both adding groups and accounts} */}
            <ModalComponent
                isModalOpen={modalProps.open}
                onClose={modalProps.method}
                message={modalProps.message}
                title={modalProps.title}
                buttons={
                    <Grid container justify="center" alignItems="center">
                        <Grid item >
                            <Button
                                onClick={modalProps.method}
                                style={{
                                    margin: '0 10px',
                                    minHeight: '42px',
                                    minWidth: '150px',
                                    fontSize: 14
                                }}
                                variant={'contained'}
                                color={'primary'}>
                                Okay
                            </Button>
                        </Grid>
                    </Grid>
                }
            />


        </>
    )
}