//#region Local Imports
import { ActionConsts } from './ActionConsts';
//#endregion Local Imports

//#region Interface Imports
import { IAction } from 'Components/Actions/IAction';
import { IPage2 } from './IPage2';
//#endregion Interface Imports

/**
 * INITIAL_STATE
 */
const INITIAL_STATE: IPage2.IStateProps = {};

type IMapPayload = IPage2.Actions.IMapPayload;

export const Page2Reducer = (state = INITIAL_STATE, action: IAction<IMapPayload>) => {
	switch (action.type) {
		case ActionConsts.Page2.SetReducer:
			return {
				...state,
				...action.payload,
			};

		case ActionConsts.Page2.ResetReducer:
			return INITIAL_STATE;

		default:
			return state;
	}
};
