import Fetch, { requestOption } from '../index'

const fetch = new Fetch();

export const getUserList = (
    limit: number,
    offset: number,
    where?: any,
    order?: any,
    query?: any
) => {

    let option: requestOption = {
        method: 'GET'
    }

    let filter: any = {
        limit,
        offset
    };

    if (order && order !== '') {
        filter.order = order;
    }

    if (where && Object.keys(where).length > 0) {
        filter.where = where;
    }

    let params = {
        filter: JSON.stringify(filter)
    }

    if (query && query !== '') {
        params['query'] = query;
    }


    console.timeLog(new URLSearchParams(params).toString())

    return fetch.call(option, `accounts/list?${new URLSearchParams(params).toString()}`)
}

export const searchUser = (
    limit: number,
    offset: number,
    query: any,
    where?: any,
    order?: any
) => {

    let option: requestOption = {
        method: 'GET'
    }

    let filter: any = {
        limit,
        offset,
        query: query
    };

    if (order && order !== '') {
        filter.order = order;
    }

    if (where && Object.keys(where).length > 0) {
        filter.where = where;
    }

    return fetch.call(option, `accounts/search?${new URLSearchParams(filter).toString()}`)
}

export const getUserDetails = (id: string) => {

    let option: requestOption = {
        method: 'GET'
    }

    return fetch.call(option, `accounts/list/${id}`)
}

export const updateUserDetails = (id, body) => {
    let option: requestOption = {
        method: 'PUT',
        body: JSON.stringify(body)
    }

    return fetch.fetchAccessToken().then(() => fetch.call(option, `user/${id}`, true))
}

